@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syncopate&display=swap);
:root {
  --backgroundColor: #E4E3E3;
  --accentColor1: #A2DBFA;
  --accentColor: #84A9AC;
  --topicalColor: #3B6978;
  --overshadow: #204051;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}



nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 100px ;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: row-end;
  font-family: 'Raleway-', sans-serif;
}

nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo{
  position: -webkit-sticky;
  position: sticky;
  padding: 12px;
  border-radius: 48%;
  font-size: 2rem;
  font-weight: 900;
  text-decoration: none;
  color: #ffffff;
}


.logo:hover{
  transition: 1s ease;
  background-color: var(--topicalColor);
  cursor: pointer;
}

.nav-links {
  list-style: none;
  margin-left: 20px;
  color: var(--accentColor);

  text-decoration: none;
  padding: 10px 10px 0px;
  border-radius: 50px;
}

.nav-links:hover{
  transition: 20ms;
  border-bottom: solid;
  cursor: pointer;
  border-color: var(--accentColor);
}
